	module.exports = {"data":{"post":{"html":"<p><a href=\"http://scriptingnews.userland.com/backissues/2001/07/11\">Scripting News</a> today mentions Pet-Food-Mania as the height of dot-com insanity. The <a href=\"http://inessential.com/2001/07/09.html\">demise</a> of <a href=\"http://www.scripting.com/images/webvanceasesoperations.gif\">Webvan</a> shows that you can't even sell people food on the Internet these days. In Germany we haven't had a dot-com crash yet, and we could still buy groceries at <a href=\"http://www.einkauf24.de\">Einkauf24.de</a> or at <a href=\"http://www.shopping24.de\">Shopping24.de</a>. But we don't. I'm not sure if they even deliver to Lüllau.</p>\n<p><a href=\"http://andrea.editthispage.com\">Andrea</a> asks today: <a href=\"http://andrea.editthispage.com/2001/07/11\">What do you want to do when you grow up?</a> I don't know, I'm not grown up yet. \";->\"</p>","fields":{"title":"Pet Food Mania","date":"11 Jul 2001","url":"https://papascott.de/2001-07-11-pet-food-mania/"}}},"pathContext":{"slug":"/2001-07-11-pet-food-mania/","prev":{"fields":{"title":"Up all night","date":"08 Jul 2001","slug":"/2001-07-08-up-all-night/"}},"next":{"fields":{"title":"Temporarily Closed Due to Illness","date":"13 Jul 2001","slug":"/2001-07-13-temporarily-closed-due-to-illness/"}}}}

